.header{
  width: 100%;
  height: 200px;
  margin-bottom: 20px;
  position: relative;
}
.header img{
  object-fit: cover;
  object-position: center;
  height: 100%;
  width: 100%;
  position: absolute;
  z-index: 10;
}
.header .text-box{
  position: absolute;
  z-index: 11;
  bottom: 20%;
}
.header h1{
  font-size: 2vw;
  color: #FAFAFA;
}

.header p{
  font-size: 1.2vw;
  color: rgb(224, 225, 240);
  margin: 0;
}
.post-item{
    margin-bottom: 20px;
}

.post-item .footer{
    display: flex;
    align-items: center;
    justify-content: space-between
}

.post-item .toolbar{
    display: flex;
    align-items: center;
    justify-content: space-between
}
.toolbar{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.sort-icon{
  cursor: pointer;
}
.edit-user{
    cursor: pointer;
}
